import React from "react";
import { Typography } from "@mui/material";

export default function Meaning(props) {
  return (
    <>
      <div className="meaning">
        <Typography sx={{ mb: 1, fontStyle: "italic" }} color={props.theme === "light" ? "text.secondary": "#ccc"}>
          {props.meaning.partOfSpeech}
        </Typography>
        {props.meaning.definitions.map((definition, index) => {
          return (
            <div key={index}>
              <li
                className="meaningLi"
                style={{ marginLeft: "1.5rem", fontSize: "1.2rem", color: props.theme === "dark" ? "white" : "black" }}
              >
                <Typography sx={{ mb: .2, fontSize: '1rem' }} variant="body2" color={props.theme === 'light'? 'text.primary' : '#ddd'}>
                  {definition.definition}
                </Typography>
              </li>
                <Typography sx={{ mb: 1.5, fontSize: '.9rem', marginLeft: '2rem' }} color={props.theme === "light"? `text.secondary` : '#aaa'}>
                    {definition.example === undefined ? "" : `"${definition.example}"`}
                </Typography>
            </div>
          );
        })}
      </div>
    </>
  );
}
