import {
  Button,
  Card,
  CardContent,
  TextField,
  Tooltip,
  Typography,
} from "@mui/material";
import VolumeUpIcon from "@mui/icons-material/VolumeUp";
import React, { useState } from "react";
import Meaning from "./Meaning";
import LoadingBar from "react-top-loading-bar";
import LoaderSpinner from "./LoaderSpinner";
import Snackbar from "@mui/material/Snackbar";

export default function SearchWord(props) {
  const [error, setError] = useState(false);
  const [phonetic, setPhonetic] = useState("");
  const [audio, setAudio] = useState("");
  const [meanings, setMeanings] = useState([]);
  const [searchWord, setSearchWord] = useState("");
  const [searchedWord, setSearchedWord] = useState("");
  const [showMeanings, setShowMeanings] = useState(false);
  const [progress, setProgress] = useState(0);
  const [audioTitle, setAudioTitle] = useState("Listen");
  const [fetching, setFetching] = useState(false);
  const [snackbar, setSnackbar] = useState(false);

  const fetchMeaning = (word) => {
    setProgress(10);
    setFetching(true);
    let api = `https://api.dictionaryapi.dev/api/v2/entries/en/${word}`;
    fetch(api)
      .then((res) => res.json())
      .then((data) => {
        setProgress(30);
        setPhonetic(data[0].phonetic);
        setMeanings(data[0].meanings);
        setSearchedWord(data[0].word);
        setProgress(60);
        setShowMeanings(true);
        setError(false);
        setProgress(100);
        if (data[0].phonetics.length === 0) {
          setAudio("");
          setAudioTitle("No audio available");
        } else {
          setAudio(data[0].phonetics[0].audio);
          setAudioTitle("Listen");
        }
        setFetching(false);
      })
      .catch((err) => {
        setError(true);
        setProgress(100);
        setFetching(false);
        console.log(err);
      });
  };

  const handleClose = () => {
    setSnackbar(false);
  };

  const playAudio = () => {
    if (audio === "") {
      setSnackbar(true);
      setAudioTitle("No audio available");
      return;
    }
    let sound = new Audio(audio);
    sound.play();
  };

  return (
    <>
      <LoadingBar
        color="dodgerblue"
        style={{ zIndex: 1000, position: "fixed", top: 0, height: "4px" }}
        height={3}
        waitingTime={800}
        progress={progress}
        onLoaderFinished={() => setProgress(0)}
      />

      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={snackbar}
        onClose={handleClose}
        message="No audio found!"
      />
      <Card sx={{ padding: 1, backgroundColor: props.theme === "light"? "#f8f8f8": "#454545" }} raised>
        <CardContent>
          <Typography
            sx={{ mb: 2.5 }}
            variant="h3"
            component="div"
            color={props.theme === "light" ? "text.primary" : "#f2f2f2"}
          >
            Search Word
            <p
              style={{
                fontSize: "11px",
                color: props.theme === "light" ? "#222" : "#ddd",
              }}
            >
              <b>
                <i>Source</i>
              </b>
              : Dictionary API
            </p>
          </Typography>
          <div
            className="searchContainer"
            style={{ display: "flex", gap: "2rem", alignItems: "baseline" }}
          >
            <TextField
              sx={{ mb: 1.5, input: { color: props.theme === "light"? "text.primary":"#ccc" } }}
              InputLabelProps={{ style: { color: props.theme === "light"? "primary" : "#ccc" } }}
              error={error}
              id="standard-error-helper-text"
              label={error ? "Word not found" : "Search here"}
              value={searchWord}
              onChange={(e) => {
                setSearchWord(e.target.value);
              }}
              placeholder="Eg: Hello"
              helperText={error ? "Check your word" : ""}
              variant="standard"
              // color={props.theme === "light"? "primary":"secondary"}
            />
            <Button
              disabled={searchWord === "" ? true : false}
              variant="outlined"
              sx={{ height: "35px"}}
              onClick={() => {
                fetchMeaning(searchWord);
              }}
            >
              Search
            </Button>
          </div>
        </CardContent>
        <LoaderSpinner isfetching={fetching} />
        <CardContent
          sx={{ marginLeft: 1.5, display: !showMeanings ? "none" : "block" }}
        >
          <div
            className="searchTitle"
            style={{ display: "flex", alignItems: "center" }}
          >
            <Tooltip title={audioTitle}>
              <VolumeUpIcon
                sx={{
                  cursor: "pointer",
                  marginRight: 3,
                  transition: ".1s ease-in",
                  border: "1px solid #ccc",
                  padding: "5px",
                  borderRadius: "50%",
                  color: "dodgerblue",
                  transform: "translateY(-3px)",
                  "&:hover": { backgroundColor: "#ddd" },
                }}
                onClick={() => {
                  playAudio();
                }}
                fontSize="large"
              />
            </Tooltip>
            <Typography sx={{ mb: 1 }} variant="h6" component="h1">
              {searchedWord}
            </Typography>
          </div>
          <Typography
            sx={{ mb: 1, marginLeft: 8 }}
            color={props.theme === "light" ? "text.secondary" : "#ccc"}
          >
            {phonetic}
          </Typography>

          <div className="meanings">
            <ol style={{ listStyle: "decimal", marginLeft: "2rem" }}>
              {meanings.map((meaning, index) => (
                <Meaning key={index} meaning={meaning} theme={props.theme} />
              ))}
            </ol>
          </div>
        </CardContent>
      </Card>
    </>
  );
}
