import { Card } from "@mui/material";
import React from "react";
import GitHubIcon from '@mui/icons-material/GitHub';
import FacebookOutlinedIcon from '@mui/icons-material/FacebookOutlined';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

export default function About(props) {
  return (
    <>
      <Card sx={{ padding: 3, mb: 'auto',  backgroundColor: props.theme === "light"? "#f8f8f8": "#454545"}}>
      <div className="information">
        <h1>About</h1>
        <span className="social">
          <a href="https://github.com/Prakash-Banjade" style={{color: '#2b2b2b'}} target="_blank" rel="noopener noreferrer">
            <GitHubIcon fontSize="large" />
          </a>
          <a href="https://www.facebook.com/prakash.banjade.5832/" target="_blank" rel="noopener noreferrer" style={{color: '#149ff9', margin: '0 1rem'}}>
            <FacebookOutlinedIcon fontSize="large" />
          </a>
          <a href="https://www.linkedin.com/in/prakash-banjade-b673b2247/" target="_blank" rel="noopener noreferrer" style={{color: '#0966c4'}}>
            <LinkedInIcon fontSize="large" />
          </a>
        </span>
      </div>
        <article>
          <h2 className="about_h2" style={{color: props.theme === "light"? "#2b2b2b":"#eee"}}>What is this?</h2>
          <p className="about_p" >
            This is a dictionary app that uses the{" "}
            <a style={{color: "dodgerblue"}} href="https://dictionaryapi.dev/">dictionaryapi.dev</a> API to
            fetch the data.
          </p>
        </article>
        <article>
          <h2 className="about_h2" style={{color: props.theme === "light"? "#2b2b2b":"#eee"}}>Why did I make this?</h2>
          <p className="about_p">
            I made this to practice my React skills and to learn how to use the
            Material UI library.
          </p>
        </article>
        <article>
          <h2 className="about_h2" style={{color: props.theme === "light"? "#2b2b2b":"#eee"}}>What did I learn?</h2>
          <p className="about_p">
            I learned how to use Material UI and how to use the
            dictionaryapi.dev API.
          </p>
        </article>
        <article>
          <h2 className="about_h2" style={{color: props.theme === "light"? "#2b2b2b":"#eee"}}>What's next?</h2>
          <p className="about_p">I plan on adding a word of the day feature and a dark mode.</p>
        </article>
      </Card>
    </>
  );
}
