import React from "react";
// import { Dna, Grid } from "react-loader-spinner";
import Skeleton from '@mui/material/Skeleton';
import Typography from '@mui/material/Typography';
// import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

export default function LoaderSpinner(props) {
  const variants = ['h4', 'h3', 'body1', 'caption', 'subtitle1', 'subtitle2', 'button', 'overline', 'srOnly'];
  return (
    <>
    <div style={{width: '90', margin: '1rem auto'}}>
      {variants.map((variant) => (
        <Typography component="div" key={variant} variant={variant}>
          {props.isfetching ? <Skeleton /> :''}
        </Typography>
      ))}
    </div>
      {/* <Dna
        visible={props.isfetching}
        height="80"
        width="80"
        ariaLabel="Loading"
        wrapperStyle={{ border: "none", margin: "50px auto", display: "block" }}
        wrapperClass="dna-wrapper"
      /> */}
      {/* <Grid
        visible={props.isfetching}
        height="50"
        width="50"
        color="grey"
        ariaLabel="Loading"
        radius="12.5"
        wrapperStyle={{margin: "50px auto", display: "flex", justifyContent: "center" }}
        wrapperClass=""
      /> */}
    </>
  );
}
